import { WelcomeLayout } from 'shared/layouts/WelcomeLayout';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { KntInput } from 'shared/components/Fields';
import Actions from 'store/actions';
import { AxiosError } from 'axios';
import { useHistory } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import React from 'react';
const FormRequest = Actions.entity.formRequest;

const validationSchema = yup.object({
	username: yup.string().email('Enter a valid email').required('Email is required')
});

function RequestPassword() {
	const dispatch = useDispatch();
	const history = useHistory();

	const formik = useFormik({
		initialValues: {
			username: ''
		},
		validationSchema: validationSchema,
		onSubmit: (values, { setFieldError, setSubmitting }) => {
			dispatch(
				FormRequest({
					entity: 'request-password',
					name: 'request-password',
					url: '/user/forgot-password/',
					method: 'post',
					params: {},
					values: { ...values, is_client: false },
					cb: {
						success: (data: any) => {
							history.push(`/change-password/?username=${data.username}`);
						},
						error: (error: AxiosError) => {
							console.log('error', error.message);
							setFieldError('username', error.response?.data.message);
						},
						finally: () => {
							setSubmitting(false);
						}
					}
				})
			);
			// alert(JSON.stringify(values, null, 2));
		}
	});

	return (
		<WelcomeLayout
			title={'Get started with KENNEKT'}
			description={'The most effective business management system'}
			goToPageLink={'/signin'}
			goToPage={'Have you remembered the password?'}
			goToPageLinkText={'Sign In'}
			back
			content={
				<>
					<form onSubmit={formik.handleSubmit}>
						<Helmet>
							<title>KENNEKT | Request password</title>
						</Helmet>

						<div className=" relative w-full font-poppins md:w-[423px]">
							<KntInput
								name={'username'}
								placeholder={'Please, enter your email to recover password'}
								label={'Email'}
								required
								onChange={formik.handleChange}
								type={'email'}
								value={formik.values.username}
								error={formik.touched.username && Boolean(formik.errors.username)}
								helperText={formik.touched.username && formik.errors.username}
							/>

							<button
								disabled={formik.isSubmitting}
								className={
									'bg-primary py-3.5 uppercase transition ' +
									'w-full rounded-lg text-center text-sm font-light text-white hover:bg-primary-dark'
								}
							>
								{formik.isSubmitting ? 'Sending new code... Please wait a while' : 'Send new code'}
							</button>
						</div>
					</form>
				</>
			}
		/>
	);
}

export default RequestPassword;
