/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';

import config from 'config';
import { Link } from 'react-router-dom';
import { useHistory } from 'react-router';
import clsx from 'clsx';
import { isMobile } from 'react-device-detect';
import { HtmlParser } from 'shared/components';
import React from 'react';
import { FiChevronLeft } from 'react-icons/fi';
const imgPath = `${config.PUBLIC_ASSETS}img/`;

type Prop = {
	content: React.ReactNode;
	title: string;
	description: string;
	goToPage: string;
	goToPageLinkText: string;
	goToPageLink: string;
	back?: boolean;
	isInitialPage?: boolean;
};

export function WelcomeLayout({
	content,
	title,
	description,
	goToPage,
	goToPageLinkText,
	goToPageLink,
	back,
	isInitialPage
}: Prop) {
	const history = useHistory();
	// @ts-ignore
	return (
		<div className={'welcome flex min-h-full font-poppins '}>
			<div
				className="welcome__left hidden bg-cover bg-no-repeat py-14 px-20 md:block md:w-[52%]"
				css={css`
					background-image: url(${imgPath}bg-welcome.jpg);
				`}
			>
				<Link to={'/'} className={'h-8 w-40'}>
					<img src={`${imgPath}logo-kennekt-2.svg`} alt="" className={'object-cover'} />
				</Link>
			</div>
			<div className="flex w-full flex-col justify-between p-4 md:block md:w-[48%] md:pl-12 md:pt-14 md:pr-20 md:pb-14">
				<div
					className={clsx('top', {
						'flex items-center justify-between': isMobile && isInitialPage
					})}
				>
					<div className="mobile-logo mb-2 lg:hidden">
						<Link to={'/'} className={'block h-6 w-32'}>
							<img src={`${imgPath}logo-kennekt-2.svg`} alt="" className={'object-cover'} />
						</Link>
					</div>
					<div className={clsx('top-nav flex justify-between md:mb-28')}>
						<div
							className={clsx(
								'group flex cursor-pointer items-center text-kgrey-welcome',
								!back && 'opacity-0',
								isInitialPage && 'hidden',
								!isInitialPage && !isMobile && 'md:flex'
							)}
							onClick={() => history.goBack()}
						>
							<span className="text-2xl transition group-hover:text-primary">
								<FiChevronLeft />
							</span>
							<span
								className={
									'mr-4 text-sm font-medium transition group-hover:text-primary md:ml-3 md:text-base'
								}
							>
								Back
							</span>
						</div>
						<div className="flex items-center text-sm font-light md:text-lg">
							<span className={'hidden text-kgrey-welcome md:block '}>{goToPage}</span>
							<Link
								to={goToPageLink}
								className={
									'transitions ml-2 whitespace-nowrap text-primary hover:text-primary-dark '
								}
							>
								{goToPageLinkText}
							</Link>
						</div>
					</div>
				</div>

				<div className={'content-wrapper'}>
					<div className="welcome__header mb-8 md:mb-12">
						<div className="mb-1 text-2xl font-bold md:mb-2.5 md:text-3xl">{title}</div>
						<p className={'text-sm font-light text-kgrey-welcome md:text-base'}>
							{/*@ts-ignore*/}
							<HtmlParser text={description} />
						</p>
					</div>
					<div className="welcome__content">{content}</div>
				</div>
				<div className={'opacity-0'}>Footer</div>
			</div>
		</div>
	);
}
